.CardInfo{
  position: relative;
  margin-top: 20px;
  h1{
    display: block;
    font-size: 25px;
    border-bottom: 1px solid #000;
    width: 30%;
    padding-bottom: 5px;
  }
}
.BlockImg{
  float: left;
  width: 30%;
  min-width: 300px;
  margin-top: 20px;
  margin-right: 50px;
  .litleImg{
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    img{
      margin-top: 10px;
      margin-right: 10px;
      width: 40px;
    }
  }
}
.Discription{
  .BlockSize{
    margin-bottom: 15px;
    li{
      margin-top: 3px;
      display: inline-block;
      border: 1px solid #000;
      text-align: center;
      width: 35px;
      border-radius: 5px;
      margin-right: 2px;
    }
  }
  .BlockColor{
    display: inline-block; 
    width: 15px; 
    height: 15px; 
    margin-right: 2px;
    margin-bottom: 15px;
  }
  .oldPrice{
    display: block; 
    margin: 20px 0 -20px 0;
    font-size: 19px; 
    text-decoration: line-through solid red 3px;
    font-weight: bold;
    color: rgba(0, 0, 0, .5);
  }
  .BlockPrice{
    display: flex;
    margin-top: 20px;
    .spanPrice{
      color: rgb(253, 2, 2);
      display: block; 
      margin-right: 20px; 
      font-size: 22px; 
      font-weight: bold;
    }
  }
  .Btn{
    background-color: #E8F7FF;
    border-radius: 5px;
    border: 1px solid #abd9f1;
    padding: 5px 10px;
    transition: .5s;
  }
  .Btn:hover{
    background-color: #abd9f1;
  }
  .blockDiscription{
    clear: both;
    width: 400px;
    p{
      font-weight: bold;
      span{
        font-weight: normal;
      }
    }
  }
}
.Information{
  position: absolute;
  right: 0;
  h5{
    padding-left: 27px;
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: left center;
    margin-top: 10px;
  }
  p{
    font-size: 15px;
    padding-left: 22px;
    background: url('https://image.flaticon.com/icons/png/512/3810/3810036.png') no-repeat left center;
    background-size: 12px;
  }
}