.Form{
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  div{
    width: 100%;
  }
  label{
    font-weight: bold;
  }
  input, select, textarea{
    display: block;
    width: 100%;
  }
  
}
.btn{
  background-color: #000;
  margin-top: 20px;
  border-radius: 10px;
  border-color: #fff;
  color: #fff;
  padding: 4px 15px;
}
.btn:hover{
  background-color: #fff;
  color: #000;
  font-weight: bold;
  border-color: #000;
}

.colorBlock{
  display: flex;
  justify-content: space-between;
  label{
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  }
  div{
    text-align: center;
  }
}