
.Nav{
  margin-top: 6px;
  position: absolute;
  display: block;
  transition: 1s;
  transform-origin:  left center;
  left: 0;
  top: 0;
  background-color: rgba(160, 153, 153, 0.267);
  width: 220px;
  padding: 0 10px 50px 10px;

  b{
    cursor: pointer;
  }
  b:hover{
    text-decoration: underline;
  }
  a{
    text-decoration: none;
    color: #000;
  }
  input{
    margin-right: 5px;
    
  }
  input[type="checkbox"]{
    cursor: pointer;
  }
  input[type="number"]{
    width: 50px;
    outline: none;
    margin: 0 5px
  }
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
  }
  input[type="number"] {
    -moz-appearance: textfield;
  }
  input[type="number"]:hover,
  input[type="number"]:focus {
    -moz-appearance: number-input;
  }

}

.DeleteNav{
  display: none;
  width: 30px;
  height: 30px;
  position: absolute;
  left: 5px;
  margin-top: 10px;
  cursor: pointer;
}

.Cross{
  position: absolute;
  top: 0;
  right: 10px;
  cursor: pointer;
}
.SubBlock{
  transform: scaleY(1);
  transition: .5s ease;
  transform-origin: 0 0;
  overflow-y: scroll;
  height: 250px; 
}
.triangle{
  display: inline-block;
  margin-left: 5px;
  width: 0;
	height: 0;
	border-left: 4px solid transparent ;
	border-top: 8px solid rgb(110, 110, 110);
	border-right: 4px solid transparent;
}