.Carusel{
  margin: 50px auto 100px auto;
  display: flex;
  justify-content: center;
  position: relative;
  overflow: hidden;
  height: 300px;
  width: 95%;
}
.I {
  border: solid #333;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 20px;
}
.Left{
  position: absolute;
  top: 120px;
  left: 0;
  width: 50px;
  height: 50px;
  transform: rotate(135deg);
  z-index: 1000;
  cursor: pointer;
  opacity: .2;
  transition: .5s;
}
.Left:hover{
  opacity: 1;
  
}
.Right{
  position: absolute;
  top: 120px;
  right: 0;
  width: 50px;
  height: 50px;
  transform: rotate(-45deg);
  z-index: 1000;
  cursor: pointer;
  opacity: .2;
  transition: .5s;
}
.Right:hover{
  opacity: 1;
  
}

.Item{
  width: 92%;
  border-top: none;
  border-bottom: none;
  position: relative;
  overflow: hidden;
}
.Elements{
  width: 100%;
  display: flex;
  // justify-content: start;
  position: absolute;
  transition: .5s ease-in-out;
}

.Nav{
  width: 250px;
  height: 7px;
  position: absolute;
  bottom: 0;
  left: 50%;
  display: flex;
  div{
    width: 18px;
    height: 100%;
    margin-right: 7px;
    border: 1px solid #333;
    cursor: pointer;
  }
}