.Card{
  border: 1px solid #000;
  min-width: 240px;
  max-width: 240px;
  width: 240px;
  height: 270px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  margin: 5px;
  img{
    text-align: center;
    width: 100%;
    height: 200px;
    object-fit:contain;
  }
  i{
    margin-top: auto;
  }
  i:hover{
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.Title{
  height: 100%;
  margin-top: 5px;
  div:first-child{
    display: flex;
    justify-content: space-between;
    margin: 0 10px; 
  }
  
}

.Price{
  flex-direction: column;
  margin-top: auto;
}
.oldPrice{
    color: rgb(155, 154, 154);
    text-decoration: line-through;
    text-decoration-color: #000;
    text-decoration-thickness: 2px;
    display: block;
    margin: 5px 0 -5px 0;
}
.price{
  color: rgb(241, 12, 12);
  font-weight: bold;
}

.Size{
  display: block;
  margin-top: auto;
  border: 1px solid #000;
  padding: 0 5px;
}

.Name{
  left: 0;
  right: 0;
  padding: 0;
  position: absolute;
  transform: scaleY(0);
  background-color: rgba(10, 10, 10, 0.301);
  transition: 0.5s ease-in-out;
  transform-origin: 0 0;
  h5{
    color: #000;
    font-weight: bold;
  }
}

.Card:hover .Name{
  transform: scaleY(1);

}
