


.Nav{
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  
  li{
    list-style: none;
  }
 
}

.tel{
  width: 150px;
  margin: auto auto auto auto;
  span{
    font-size: 14px;
    display: block;
    padding-left: 60px;
    background-image: url('https://image.flaticon.com/icons/png/512/889/889154.png'),
      url('https://image.flaticon.com/icons/png/512/2111/2111646.png'),
      url('https://image.flaticon.com/icons/png/512/408/408771.png');
    background-position: left center, 20px center, 40px center;
    background-size: 13px, 13px, 13px;
    background-repeat: no-repeat, no-repeat, no-repeat;
  }
}

.LOGO{
  margin-right: 50px;
  img{
    max-width: 100px;
    min-width: 100px;
  }
}
.Search{
  width: 100%;
  margin: auto 0;
  input{
    border-color: #E8F7FF;
    border-radius: 11px;
    outline: none;
    min-width: 105px;
    width: 75%;
  }
  button{
    margin-left: 5px;
    background-color: #E8F7FF;
    border-color:transparent;
    border-radius: 11px;
    width: 20%;
    min-width: 82px;
  }
}
.leftUl{
  display: flex;
  justify-content: start;
  width: 60%;
}
.rightUl{
  align-items: center;
  display: flex;
  justify-content: space-between;
  min-width: 110px;
  // width: 10%;
  li{
    position: relative;
  }
  img{
    width: 40px;
  }
  
}
.user{
  a{
    color: #000;
    text-decoration: none;
    font-size: 14px;
  }
}
.SubNav{

  background-color: #E8F7FF;
  ul{
    // position: relative;
    padding: 10px 0;
    margin: auto 0;
    display: flex;
    .liCatalog{
      
      padding-left: 20px;
      background-image: url('https://image.flaticon.com/icons/png/512/152/152523.png');
      background-repeat: no-repeat;
      background-position: left 4px;
      background-size: 15px;
      

    }
    .sale{
      a{
        font-weight: bold;
        color: rgb(207, 23, 23);
      }
    }
    li{
      list-style: none;
      margin-left: 20px;



        background-image: linear-gradient(currentColor, currentColor);
        background-position: 0% 22px;
        background-repeat: no-repeat;
        background-size: 0% 2px;
        transition: background-size .3s ease;
        cursor: pointer;

      a{
        text-decoration: none;
        color: #000;
      }
    }
    li:hover:not(.liCatalog){
      background-size: 100% 2px;
    }
  }
}
.subBar{
  display: none;
  z-index: 10;
  width: 170px;
  border-right: 1px solid #000;
  flex-direction: column;
  li{
    position: relative;
  }
}

.subSubBar{
  position: absolute;
  top: -10px;
  left: 150px;
  z-index: 1000;
  visibility: hidden;
  height: 100px;
  width: 200%;
  flex-direction: column;
  flex-wrap: wrap;
  // border: 1px solid #000;

}
.liCatalog:hover .Wrapli{
  display: block;
}
.Wrapli{
  z-index: 1000;
  padding: 15px;
  background-color: #E8F7FF;
  width: 93%;
  height: 150px;
  position: absolute;
  top: 130px;
  left: 55px;
  display: none;
}
.subBarLi1:hover .subSubBar, .subBarLi2:hover .subSubBar2, .subBarLi3:hover .subSubBar3{
  visibility: visible;
}

.subSubBar2{
  position: absolute;
  top: -35px;
  left: 150px;
  z-index: 1000;
  visibility: hidden;
  height: 100px;
  width: 500%;
  flex-direction: column;
  flex-wrap: wrap;
  // border: 1px solid #000;

}
.subSubBar3{
  position: absolute;
  top: -55px;
  left: 150px;
  z-index: 1000;
  visibility: hidden;
  height: 100px;
  width: 200%;
  flex-direction: column;
  flex-wrap: wrap;
  // border: 1px solid #000;

}